.contacto {
    width: 50%;
    max-width: 700px;
    height: 300px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 36px;
    color: var(--blanco);
    font-weight: 600;
    padding: 1rem;
    margin-bottom: 2rem;
    background-color: var(--gris);
    border-radius: 1rem;
    box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.6);
    transition: .3s ease-in-out;
}


.contacto:hover {
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.6);

}

.contacto p {
    font-size: 16px;
    color: var(--claro);
    text-wrap: pretty;
    margin: 1rem 2rem;
}

.contacto-container {
    width: 100%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.contacto-item {
    width: 90%;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    margin-bottom: 10px;
    font-size: 18px;
    font-weight: 400;
    color: var(--claro);
}

.contacto-item .item {
    color: var(--blanco);
    font-weight: 600;
    text-align: start;
    margin: 0rem 1rem;
}

.contacto-item-btn {
    background-color: var(--primario);
    color: var(--negro);
    font-weight: 600;
    border: none;
    border-radius: .5rem;
    padding: .5rem 1rem;
    cursor: pointer;
    transition: .3s ease-in-out;
}

.contacto-item-btn:hover {
    filter: saturate(250%);
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.6);
}


.popup {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: rgba(0, 0, 0, 0.5);
    color: var(--blanco);
    padding: 1rem;
    border-radius: 1rem;
    z-index: 1000;
}

.popup {
    font-size: 16px;
}

@keyframes fadeInOut {
    0% {
        opacity: 0;
    }

    50% {
        opacity: 1;
    }

    100% {
        opacity: 0;
    }
}

.popup {
    animation: fadeInOut 2s ease;
}


.contacto-btn-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 1rem;
    width: 100%;
    height: auto;
    margin-top: 1rem;
    margin-bottom: 2rem;
}


@media only screen and (max-width: 850px) {
    .contacto {
        width: 70%;
    }
}

@media only screen and (max-width: 430px) {
    .contacto {
        width: 90%;
        height: auto;
        font-size: 26px;
    }


    .contacto p {
        font-size: 15px;
        margin: 0rem;
        text-align: center;
        margin-bottom: 1rem;
    }
    .contacto-item {
        width: 100%;
        align-items: center;
        justify-content: center;
        font-size: 15px;
    }

}